module.exports = `html
<template>
  <div>
    <v-show-at small>       </v-show-at>
    <v-show-at medium>      </v-show-at>
    <v-show-at large>       </v-show-at>
    <v-show-at xlarge>      </v-show-at>
    <v-show-at no-match>    </v-show-at> // Bootstrap 4 "xs"
  </div>
</template>
`



